import tabs from '../../components/ms-tabs/ms-tabs.vue';
export default {
	components: { tabs },
	data() {
		return {
			list: [
				{
					title: '学习情况统计'
				},
				{
					title: '学习成绩'
				}
			],
			active: '0',
			dataArr: [
				{
					userName: '上课人',
					className: '语文',
					progress: '50%',
					lastTime: '2020-02-26',
					teacher: '王某'
				}
			]
		};
	},
	watch: {
		active: {
			immediate:true,
			handler(val) {
				if (val == '0') {
					// 学习进度
					this.dataArr = new Array(6).fill({
						userName: '上课人',
						className: '语文',
						progress: '50%',
						lastTime: '2020-02-26',
						teacher: '王某'
					});
				} else if (val == '1') {
					//学习结果
					this.dataArr = new Array(3).fill({
						userName: '上课人',
						className: '语文',
						progress: '50%',
						lastTime: '2020-02-26',
						teacher: '王某'
					});
				}
			}
		}
	},
	onLoad(evt) {},
	methods: {
		getData() {}
	}
};
